a, a:hover{
    color: black;
    text-decoration: none;
}

.goExam{
    cursor: pointer;
}

.project{
    background-color: white;
    width: 45%;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
    margin-bottom: 50px;
    text-overflow: ellipsis;
    padding: 0 20px;
    box-shadow: 0 8px 38px rgba(133, 133, 133, 0.3), 0 5px 12px rgba(133, 133, 133, 0.22);
}

.project_column{
    width: 40%;
    box-sizing: border-box;
    text-overflow: ellipsis;
}

.project_column:last-child{
    padding: 20px 0;
    width: 50%;
}

.project_header{
    display: flex;
}

.project_thumnail{
    max-width: 100%;
    position: relative;
    top: -25px;
    cursor: pointer;
    transition: 0.3s;
    box-shadow: -10px 19px 38px rgba(83, 83, 83, 0.3), 10px 15px 12px rgba(80, 80, 80, 0.22);
}

.project_thumnail:hover{
    opacity: 0.7;
}

.modal{
    display: none;
    position: fixed;
    z-index: 1;
    padding-top: 100px;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgb(0, 0, 0);
    background-color: rgba(0, 0, 0, 0.9);
}

.modal-content{
    margin: auto;
    display: block;
    width: 80%;
    max-width: 700px;
}

#caption{
    margin: auto;
    display: block;
    width: 80%;
    max-width: 700px;
    text-align: center;
    color: #ccc;
    padding: 10px 0;
    height: 150px;
}

.modal-content, #caption{
    animation-name: zoom;
    animation-duration: 0.6s;
}

@keyframes zoom{
    from {transform: scale(0);}
    to {transform: scale(1);}
}

.close{
    position: absolute;
    top: 15px;
    right: 35px;
    color: #f1f1f1;
    font-size: 40px;
    font-weight: bold;
    transition: 0.3s;
}

.close:hover, 
.close:focus {
    color: #bbb;
    text-decoration: none;
    cursor: pointer;
}

@media only screen and (max-width: 700px){
    .modal-content{
        width: 100%;
    }
}

.project_skills_ul{
    list-style: none;
    display: flex;
    padding-left: 0;
    text-overflow: ellipsis;
    flex-wrap: wrap;
}

.project_skills_li{
    margin-right: 5px;
}

@media screen and (min-width: 320px) and (max-width: 667px){
    .project{
        width: 100%;
    }
}
@media screen and (min-width: 320px) and (max-width: 667px) and (orientation: portrait){
    .project{
        width: 100%;
        flex-direction: column;
    }
    .project_thumnail{
        top: 0;
        left: 0;
        width: 100%;
    }
    .project_column{
        width: 100%!important;
    }
}

.project_title_ul{
    list-style: none;
    padding-left: 20px;
}

.project_title_li{
    display: inline-block;
}

.project_title_img{
    width: 30px;
    margin-right: 10px;
}